.reviews {
  &__swiper-prev,
  &__swiper-next {
    width: 40px;
    height: 40px;
    background: $purple;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .3s;
    &.swiper-button-disabled {
      pointer-events: none;
      background-color: #a46cec70;
    }
  }
  &__swiper-desk-prev,
  &__swiper-desk-next {
    width: 70px;
    height: 70px;
    background: $purple;
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color .3s;
    &:hover {
      background-color: black;
    }
    &.swiper-button-disabled {
      pointer-events: none;
      background-color: #a46cec70;
    }
  }
  &__pop-swiper-prev,
  &__pop-swiper-next {
    width: 40px;
    height: 40px;
    background: $purple;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .3s;
    &.swiper-button-disabled {
      pointer-events: none;
      background-color: #a46cec70;
    }
  }
  &__pop-swiper-desk-prev,
  &__pop-swiper-desk-next {
    display: none;
    width: 70px;
    height: 70px;
    background: $purple;
    border-radius: 10px;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    z-index: 50;
    transform: translateY(-50%);
    transition: background-color .3s;
    @media (min-width: 1024px) {
      transform: translateY(-70%);
      display: flex;
    }
    &:hover {
      background-color: black;
    }
    &.swiper-button-disabled {
      pointer-events: none;
      background-color: #a46cec70;
    }
  }
  &__pop-swiper-desk-prev {
    left: 0px;
  }
  &__pop-swiper-desk-next {
    right: 0px;
  }
  &__read-more {
    margin-top: 10px;
    display: none;
    cursor: pointer;
    font-size: 14px;
    font-weight: 600;
    font-family: "manrope",sans-serif;
    text-decoration: underline;
    &.active {
      display: block;
    }
  }
  &__slide {
    @media (min-width: 768px) {
      height: auto;
    }
  }
  &__pop-more {
    &.opened {
      opacity: 1;
      pointer-events: visible;
    }
  }
 &__pop-swiper-scrollbar {
   .swiper-scrollbar-drag {
     background-color: $beige;
     height: 6px;
   }
 }
  &__pop-original {
    &.opened {
      opacity: 1;
      pointer-events: visible;
    }
  }
}