.present {
  form {
    margin: 0 auto;
    width: 100%;
    & > p > span {
      display: flex;
      gap: 31px;
      flex-direction: column;
      @media (min-width: 768px) {
        flex-direction: row;
      }
      span.wpcf7-form-control-wrap {
        display: flex;
        @media (min-width: 1024px) {
          width: 100%;
        }
      }
      textarea {
        padding: 10px;
        resize: none;
        min-height: 96px;
        font-size: 20px;
        line-height: 180%;
        font-weight: 400;
        border: 1px solid $beige-light;
        color: #d4d4d2;
        &:focus {
          color: $white;
          border: 1px solid $white;
        }
        &::placeholder {
          color: #d4d4d2;
        }
      }
    }
  }
  &__input {
    padding: 9px 0;
    width: 100%;
    background-color: transparent;
    font-size: 20px;
    line-height: 180%;
    font-weight: 400;
    color: #d4d4d2;
    border-bottom: 1px solid $beige-light;
    transition: color .3s;
    &:focus {
      color: $white;
      border-bottom: 1px solid $white;
    }
    &::placeholder {
      color: #d4d4d2;
    }
  }
}