.program {
  &__item {
    padding-bottom: 19px;
    border-bottom: 2px solid $beige-light;
  }
  &__pop {
    &.active {
      opacity: 1;
      pointer-events: visible;
    }
    &-thanks.active {
      opacity: 1;
      pointer-events: visible;
    }
    &-form.hide {
      opacity: 0;
      pointer-events: none;
    }
    form {
      margin: 0 auto;
      max-width: 646px;
      width: 100%;
      & > p {
        display: flex;
        gap: 29px;
        flex-direction: column;
        @media (min-width: 1024px) {
          gap: 40px;
        }
        span.wpcf7-form-control-wrap {
          display: flex;
        }
        textarea {
          padding: 10px;
          resize: none;
          min-height: 96px;
          font-size: 20px;
          line-height: 180%;
          font-weight: 400;
          border: 1px solid $beige-light;
          color: #d4d4d2;
          &:focus {
            color: $white;
            border: 1px solid $white;
          }
          &::placeholder {
            color: #d4d4d2;
          }
        }
      }
    }
    &-input {
      padding: 10px 0;
      width: 100%;
      background-color: transparent;
      font-size: 20px;
      line-height: 180%;
      font-weight: 400;
      color: #d4d4d2;
      border-bottom: 1px solid $beige-light;
      transition: color .3s;
      &:focus {
        color: $white;
        border-bottom: 1px solid $white;
      }
      &::placeholder {
        color: #d4d4d2;
      }
    }
    &-submit {
      margin: 0 auto;
      font-size: 22px;
      line-height: 145%;
    }
  }
}