*{
  outline: none !important;
  box-sizing: border-box !important;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html, body{
  width: 100%;
  height: 100%;
}

body {
  font-family: "Didact Gothic",sans-serif;
  color: $black;
  background-color: $white;
}

form {
  .wpcf7-not-valid-tip {
    font-family: "Manrope", sans-serif;
    color: #ff521c;
    font-weight: 600;
    font-size: 14px;
  }
  .wpcf7-spinner {
    display: none;
  }
  input {
    transition: color .3s;
  }
  input.wpcf7-not-valid {
    color: #FF521C;
    border-bottom: 1px solid #FF521C;
  }
}

.container {
  transition: max-width 1s;
}

.text-decoration-hover {
  position: relative;
  overflow: hidden;
  display: flex;
  &:hover:after {
    transform: translateX(0);
  }
  &:after {
    content: '';
    width: 100%;
    height: 1px;
    background-color: $white;
    position: absolute;
    left: 0;
    bottom: 0;
    transform: translateX(-105%);
    transition: .5s;
  }
}