.tariffs {
  &__ul {
    li {
      padding-top: 9px;
      padding-bottom: 10px;
      border-bottom: 1px solid $white;
      font-size: 14px;
      line-height: 171%;
      display: flex;
      align-items: center;
      gap: 10px;
      @media (min-width: 1024px) {
        font-size: 20px;
        line-height: 180%;
        gap: 20px;
      }
      &:before {
        width: 24px;
        height: 24px;
        content: '';
        background-image: url('../../images/tariffs/check.svg');
        background-repeat: no-repeat;
        background-size: contain;
        flex-shrink: 0;
      }
      &:first-child {
        border-top: 1px solid $white;
        @media (min-width: 1024px) {
          border-top: 0;
          padding-top: 0;
        }
      }
    }
    &.non-active {
      @media (min-width: 1024px) {
        padding-top: 10px;
      }
      li {
        color: #C3C4D7;
        &:first-child {
          border-top: 0;
        }
        &:before {
          opacity: .2;
        }
      }
    }
  }
  &__pop {
    &.active {
      opacity: 1;
      pointer-events: visible;
    }
    &-item {
      display: none;
      &.active {
        display: block;
      }
      &.hide {
        display: none;
      }
    }
    &-thanks {
      width: 100vw;
      height: 100vh;
      justify-content: center;
      @media (min-width: 1024px) {
        width: calc(100% - 64px);
        height: auto;
      }
      &.active {
        opacity: 1;
        pointer-events: visible;
      }
      &.hide {
        pointer-events: none;
      }
    }
  }
}