.mentor {
  .grid {
    div {
      &:nth-child(1) {
        grid-area: 1 / 1 / 4 / 2;
        @media (min-width: 1024px) {
          grid-area: 2 / 1 / 3 / 2;
        }
      }
      &:nth-child(2) {
        grid-area: 1 / 2 / 2 / 3;
        @media (min-width: 1024px) {
          grid-area: 1 / 1 / 2 / 2;
        }
      }
      &:nth-child(3) {
        justify-self: start;
        grid-area: 2 / 2 / 3 / 3;
        @media (min-width: 1024px) {
          align-self: start;
          grid-area: 3 / 1 / 4 / 2;
        }
      }
    }
  }
  &__editor {
    font-size: 18px;
    line-height: 144%;
    @media (min-width: 1024px) {
      font-size: 20px;
      line-height: 180%;
    }
    strong {
      font-weight: 400;
      text-decoration: underline;
      text-underline-offset: 4px
    }
    ul {
      list-style: disc;
      margin-left: 30px;
      li::marker {
        font-size: 14px;
      }
    }
  }
  &__title {
    position: relative;
    &:after {
      content: '';
      width: 100%;
      height: 1px;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: $beige;
    }
  }
}