.posts {
  &__slide {
    transition: .5s;
    &:hover {
      background-color: $black;
      color: $white;
      h3 {
        text-decoration: underline;
      }
    }
  }
  &__swiper-prev,
  &__swiper-next {
    width: 70px;
    height: 70px;
    background: $purple;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .3s;
    &.swiper-button-disabled {
      pointer-events: none;
      background-color: #a46cec70;
    }
  }
  &__swiper-desk-prev,
  &__swiper-desk-next {
    width: 70px;
    height: 70px;
    background: $purple;
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color .3s;
    &:hover {
      background-color: black;
    }
    &.swiper-button-disabled {
      pointer-events: none;
      background-color: #a46cec70;
    }
  }
}